// Homepage : Experts
#block-block-14,
#block-block-8,
#block-block-16 {
	.content {
		background-repeat: no-repeat;
	}
	a {
		display: block;
		text-decoration: none;

		span.default-text {
			.text-uppercase;
			color: #009FE3;
			font-size: 18px;

			span {
				font-weight: bold;
			}

			div {
				color: #4C6172;
				font-size: 14px;
			}
		}

		span.hover {
			z-index: 100;
			cursor: pointer;
			transition: all 0.5s ease 0s;
			background-color: #4C6172;
			color: #FFF;

			span {
				text-transform: uppercase;
				display: block;
			}
		}
	}

	@media (max-width: @screen-sm-min) {
		span.default-text {
			display: block;
			padding: 15px;
			div {
				.hidden();
			}
		}
		span.hover {
			display: block;
			padding: 15px;
		}
	}

	@media (min-width: @screen-sm-min) {
		.content {
			background-position: 5% 50%;
			position: relative;
		}
		a {
			padding: 4em 0.3em 2em 13em;
			height: 12em;

			span.default-text {
				span {
					display: block;
				}
			}

			span.hover {
				display: none;
				z-index: 100;
				cursor: pointer;
				transition: all 0.5s ease 0s;
				position: absolute;
				top: 0px;
				left: 0px;
				width: 100%;
				padding: 1.5em 2.12em;
				height: 12em;

				span {
					position: absolute;
					bottom: 1.5em;
				}
			}

			&:hover {
				span.hover {
					display: block;
				}
			}
		}
	}
}
@media (min-width: @screen-sm-min) {
	body.not-logged-in {
		#block-block-14 .content { background-image: url("../images/askQuestion.png"); }
		#block-block-8 .content { background-image: url("../images/launchChallenge.png"); }
