// Timeline
#block-progress-tracker-progress-tracker {
	.block-title {
		margin-top: 0;
	}
	ul.progress-tracker {
		.make-row();
		padding: 0;
		margin: 0 0 15px;

		li {
			.make-sm-column(6);
			.text-center;
			.text-uppercase;

	    list-style: outside none none;
	    margin: 0px;
	    padding: 5px 10px;

	    font-weight: 700;
	    color: #FFF;
	    background-color: #A1ACB5;

			&.first {
		    border-top-left-radius: @border-radius-base;
		    border-bottom-left-radius: @border-radius-base;
				border-right: 2px solid #fff;
			}
			&.last {
		    border-top-right-radius: @border-radius-base;
		    border-bottom-right-radius: @border-radius-base;
				border-left: 2px solid #fff;
			}
		}
	}

}


// Sponsors / Partners list
#block-views-sponsors-partners-block,
#block-views-sponsors-sponsors-block {

	.user {
		margin-bottom: 15px;
		min-height: 226px;
		.text-center;

		h3,
		h4,
		p {
			.text-left;
		}

		.btn {
			display: block;
			text-transform: none;
			padding: 0;
			white-space: normal;
			h3 {
				color: @brand-primary;
			}
			h4 {
				color: @darkerBlue;
			}
		}

		img {
			.center-block;
			.img-circle;
			background-color: @gray-lighter;

		}
		.portrait {
			display: inline-block;
			position: relative;
			margin: 20px auto 0;

			&:after {
				content: "";
				display: block;
				width: 52px;
				height: 52px;
				position: absolute;
				top: -20px;
				right: -20px;
				background-repeat: no-repeat;
				background-position: top right;
			}
		}

		.collapse {
			background-color: @gray-lighter;
			padding: 15px;
			position: relative;
			span.caret {
				border-width: 15px;
				position: absolute;
				top: 0;
			}
		}
	}
}
#block-views-sponsors-partners-block {
	.user {
		.portrait {
			&:after {
				background-image: url("../images/picto/round_blue.png");
			}
